@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.item{
  background: #87CEEB;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
.item__header{
  display: flex;
  justify-content: space-between;
}
.item__label{
  color: black;
  display: inline-block;
  margin-bottom: 10px;
  text-align: start;
}
.item__info{
  display: flex;
}
.author,.date{
  margin-left: 10px;
}
.item__comments-count{
  display: inline-block;
  font-size: 1.2rem;
  position: relative;
  margin-top: 5px;
  flex: 0 0 55px;
}
.item__comments-count::after{
  width: 16px;
  height: 16px;
  position: relative;
  content: '';
  display: inline-block;
  margin-left: 2px;
  background: url(/HN-interface/static/media/comment.a88be251.svg);
}


@media (max-width:1140px){
  .item__label{
    font-size: 1.3rem;;
  }
}
@media (max-width:740px){
  .item__label{
    font-size: 1.1rem;;
  }
}
@media (max-width:560px){
  .item__label{
    font-size: 1rem;
    margin-bottom: 6px;
  }
  .item__info{
    font-size: 0.7rem;
  }
  .item__comments-count{
    font-size: 1rem;
    flex: 0 0 auto;
  }
  .item__comments-count::after{
    display: none;
  }
  .author,.date{
    margin-left: 6px;
  }
}
.nav_btn{
  margin: 4px;
  display: inline-block;
  
}
.nav_btn div{
  padding: 10px 3px;
  cursor: pointer;
  background: #fff;
  border-radius: 5px;
  border: 1px solid #000;
}
.nav_btn div a{
  padding: 10px;
  color: #000;
}

.main__list{
  flex: 1 1 100%;
  text-align: center;
}
.comment{
  margin-left: 15px;
  background: #fff;
  border-radius: 10px;
  font-size: 0.8rem;
  margin-bottom: 5px;
}
.comment__header{
  font-weight: 700;
  padding: 5px 0 0 5px;
}
.comment__text{
  padding: 5px 0 5px 5px;
  word-wrap: break-word;
}
#descendants{
  display: none;
}
.comment__btn{
  cursor: pointer;
  padding: 5px;
  font-weight: 700;
}

.info__list{
  flex: 1 1 100%;
  background: #87CEEB;
  border-radius: 5px;
  padding: 10px;
}
.info__header{
  margin-bottom: 40px;
  text-align: center;
}
.info__label{
  margin-bottom: 15px;
  font-size: 2rem;
}
.info__url{
  color: blue;
  font-size: 1.2rem;
  word-wrap: break-word;
}
.info__block{
  font-size: 1.2rem;
  margin-bottom: 20px;
}
.info__block span{
  font-weight: 700;
}
.info__comments{
  font-size: 1.2rem;
}

@media (max-width:740px){
  .info__label{
    font-size: 1.5rem;
  }
  .info__block{
    font-size: 1rem;
    margin-bottom: 10px;
  }
}
@media (max-width:560px){
  .info__nav-btns{
    text-align: center;
  }
  .info__label{
    font-size: 1.2rem;
  }
  .info__url{
    font-size: 1rem;
  }
}
.header{
  text-align: center;
  padding: 20px 10px 5px 10px;
}
.header__label{
  display: inline-block;
}
.btns{
  display: inline-flex;
  flex-direction: column;
  position: fixed;
  margin-left: -60px;
}
.btns__btn{
  display: inline-block;
}
.btns__btn img{
  width: 40px;
  height: 40px;
}
.footer{
  padding: 20px;
}
.footer__links{
  display: flex;
  justify-content: space-around;
}
.footer__link a{
  color: black;
  font-size: 1.2rem;
}
.footer__link{
  transition: .6s;
}
.footer__link:hover{
  transform: scale(1.1);
}

@media (max-width:560px){
  .footer__link a{
    font-size: 1rem;
  }
  .footer__link:hover{
    transform: none;
  }
}
.preloader{
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.3);
  z-index: 999;
}
.preloader__img{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
}

.container{
  max-width: 1000px;
  margin: 0 auto;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
  height: 100vh;
}


@media (max-width:1140px){
  .container{
    max-width: 800px;
  }
}
@media (max-width:960px){
  .container{
    max-width: 700px;
  }
}
@media (max-width:740px){
  .container{
    max-width: 540px;
  }
}
@media (max-width:560px){
  .container{
    max-width: 100%;
  }
}
body{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
div{
  box-sizing: border-box;
}
h1,h2,h3,h4,h5,h6{
  margin: 0;
}
ul{
  padding: 0;
}
a{
  text-decoration: none;
}
p{
  margin: 0;
  padding: 0;
}
hr{
  border: 1px solid black;
}
