@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
body{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}
div{
  box-sizing: border-box;
}
h1,h2,h3,h4,h5,h6{
  margin: 0;
}
ul{
  padding: 0;
}
a{
  text-decoration: none;
}
p{
  margin: 0;
  padding: 0;
}
hr{
  border: 1px solid black;
}